
@import "../assets/css/base.css";
.setting[data-v-1d80e279]{
			width: 100%;
			height: 50px;
			position: relative;
			border-bottom: 1px solid #f7f7f7;
			z-index: 1
}
.setting span[data-v-1d80e279]:nth-child(1){
			position: absolute;
			left: 25px;
			top: 20px;
}
.setting span[data-v-1d80e279]:nth-child(2){
			float: right;
